import axios from 'axios'
import { xml2js } from 'xml-js'

export function getGame(callback) {
  axios.get('https://itch.io/games.xml')
    .then(response => {
      const games = xml2js(response.data, { compact: true }).rss.channel.item
      const index = Math.floor(Math.random() * games.length)
      const game = games[index]

      axios({
        method: 'get',
        url: 'http://lest-cst336.herokuapp.com/hw/hw4/api/game.php',
        params: {
          title: game.plainTitle._text
        }
      }).then(r => {
        callback(
          {
            title: game.plainTitle._text,
            image: game.imageurl._text,
            description: game.description._cdata.split('\n<img')[0],
            price: game.price._text,
            link: game.link._text,
            counter: Number(r.data.quantity)
          }
        )
      })
    })
}