<template>
  <v-container fill-height>
    <v-layout
      align-center 
      justify-center 
      column
    >
      <v-sheet
        class="game-display"
        color="grey lighten-3"
      >
        <v-layout fill-height column>
          <v-layout v-if="game" column>
            <Display :title="game.title" :image="game.image" :description="game.description" :counter="game.counter"/>
          </v-layout>
          <v-layout v-else align-center justify-center>
            <v-progress-circular indeterminate color="red"></v-progress-circular>
          </v-layout>
          <DisplayButtons :buttonCheckItOut="checkOutGame" :buttonNewGame="getNewGame" :isGameLoaded="game != null" :baseColor="backgroundColor"/>
        </v-layout>
      </v-sheet>
    </v-layout>
  </v-container>
</template>

<script>

import Display from '@/components/Display'
import DisplayButtons from '@/components/DisplayButtons'

import { getGame } from '@/scripts/games-api'

export default {
  components: {
    Display,
    DisplayButtons
  },
  props: {
    backgroundColor: String,
    changeBackground: Function
  },
  data() {
    return {
      game: {},
    }
  },
  methods: {
    checkOutGame() {
      window.location.href = this.game.link
    },
    getNewGame() {
      this.game = null

      getGame((game) => {
        this.game = game
      })

      this.changeBackground()
    }
  },
  mounted() {
    this.getNewGame()
  }
}

</script>

<style scoped>

.game-display {
  min-width: 50%;
  min-height: 50%;
}

</style>
