<template>
  <v-app :class="color + ' lighten-3'">
    <!-- Navbar can go here -->

    <v-content >
      <Home :backgroundColor="color" :changeBackground="pickColor"/>
    </v-content>
  </v-app>
</template>

<script>

import Home from '@/components/Home'

export default {
  name: 'App',
  components: {
    Home
  },
  data() {
    return {
      colors: ['red', 'pink', 'purple', 'deep-purple', 'indigo', 'blue', 'light-blue', 'cyan', 'teal', 'green', 'light-green', 'lime', 'yellow', 'amber', 'orange', 'deep-orange'],
      color: ''
    }
  },
  methods: {
    pickColor() {
      const index = Math.floor(Math.random() * this.colors.length)
      this.color = this.colors[index]
    }
  },
  mounted() {
    this.pickColor()
  }
}

</script>

